import flag from './flag.png';
import './App.css';
import {makeStyles, Paper} from '@material-ui/core'
import { useState } from 'react';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
// import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';



function App() {
  const [emailVerified,setEmailVerified] = useState(false)
  const [mobileVerified,setMobileVerified] = useState(false)
  const [age, setAge] = useState('');
  const [formValues,setFormValues] = useState({firstName:"",lastName:"",email:"",mobile:"",password:""})
  const [errorMsg,setErrorMsg] = useState({firstName:"",lastName:"",email:"",mobile:""})

  const handleInputChange=(event)=>{
    
    var field = event.target.name
    // alert(field)
    var value = event.target.value
    // setFormValues({...formValues,field:value})
    var formValue = {...formValues}
    formValue[field] = value
    setFormValues(formValue)
    if(value.length==0){
      var errorM = {...errorMsg}
      errorM[field] = "Enter Valid Input"
      setErrorMsg(errorM)
    }
    else{
      var errorM = {...errorMsg}
      errorM[field] = ""
      setErrorMsg(errorM)
    }
  }
  const handleChange = (event) => {
    setAge(event.target.value);
  };
  const styles=makeStyles({
    main:{
      margin:"auto",
      marginTop:"10%",
      width:"50%"
    },
    input:{
      width:"330px",
      border:"none",
      fontSize:"18px",
      '&:focus':{
        outline:"none"
      },
      // marginLeft:"-30%"
    },
    
    inputContainer:{
      height:"35px",
      width:"350px",
      borderRadius:"10px",
      border:"1px solid #ddd",
      paddingTop:"5%",
      marginTop:"-3%",
      display:"flex",
      alignItems:"flex-start",
      paddingLeft:"2%"
      
      
    },
    inputLable:{
      textAlign:"left",
      width:"300px",
      paddingLeft:"3%",
      color:"lightgray",
      fontWeight:"bold"
    },
    verifyBtn:{
      outline:"none",
      border:"none",
      backgroundColor:"transparent",
      color:"#000080",
      fontWeight:"bold",
      // marginLeft:"80px"
    },
    emailOtpContainer:{
      paddingTop:"15px",
      // paddingLeft:"3px",
      width:"50px",
      height:"40px",
      borderRadius:"10px",
      backgroundColor:emailVerified?"white":"lightgray",
      border:emailVerified?"1px solid #000080":"none",

    },
    mobileOtpContainer:{
      paddingTop:"15px",
      // paddingLeft:"3px",
      width:"50px",
      height:"40px",
      borderRadius:"10px",
      backgroundColor:mobileVerified?"white":"lightgray",
      border:mobileVerified?"1px solid #000080":"none",
    },
    otpInput:{
      border:"none",
      "&:focus":{
        outline:"none"
      },
      width:"10px",
      backgroundColor:"transparent",
      fontSize:"18px"
    },
    nextBtn:{
      width:"300px",
      height:"40px",
      backgroundColor:"#000080",
      color:"white",
      fontWeight:"bolder",
      fontSize:"24px",
    },
    formControl: {
      // margin: theme.spacing(1),
      marginLeft:"12%",
      width:"800px",
      marginBottom:"40px",
  
    },
  })
  
  const classes = styles()
  return (
    <div className="App">
      <div className = {classes.main}>

        <Paper elevation ="19" style={{width:"160%", marginLeft:"-30%"}}>
          <div style={{display:"flex",flexDirection:"column",alignItems:"center"}}>
            <div><p style={{color:"#000080"}}><b>USER REGISTRATION</b></p></div>
            <div style = {{width:"60px",backgroundColor:"#000080",height:"3px",marginTop:"-10px",borderRadius:"3px"}}></div>
          </div>
          <div style={{display:"flex",flexDirection:"column"}}>
            <div style={{display:"flex",flexDirection:"row",justifyContent:"space-between",width:"80%",margin:"auto"}}>
              <div style={{display:"flex",flexDirection:"column",alignItems:"flex-start"}}>
                <div>
                  <p className={classes.inputLable}>First Name</p>
                </div>
                <div className = {classes.inputContainer}>
                  <input className = {classes.input} name="firstName" onChange={(e)=>handleInputChange(e)} placeholder="Enter Your First Name"></input>
                </div>
                <p style={{color:"red"}}>{errorMsg.firstName}</p>
              </div>
              <div style={{display:"flex",flexDirection:"column",alignItems:"flex-start",width:"350px"}}>
                <div>
                  <p className={classes.inputLable}>Last Name</p>
                </div>
                
                <div className = {classes.inputContainer}>
                  <input className = {classes.input} name="lastName" onChange={(e)=>handleInputChange(e)} placeholder="Enter Your Last Name"></input>
                </div>
                <p style={{color:"red"}}>{errorMsg.lastName}</p>
              </div>
            </div>


            <div style={{display:"flex",flexDirection:"row",justifyContent:"space-between",width:"80%",margin:"auto"}}>
              <div style={{display:"flex",flexDirection:"column",alignItems:"flex-start"}}>
                <div>
                  <p className={classes.inputLable}>Email ID</p>
                </div>
                <div className = {classes.inputContainer}>
                  <input className = {classes.input} name="email" onChange={(e)=>handleInputChange(e)} placeholder="Enter Your Email ID"></input>
                  <button className = {classes.verifyBtn} onClick={()=>{if(formValues.email.length>0){setEmailVerified(true)}else{var errorM = {...errorMsg};errorM['email'] = "Provide Valid Email id";setErrorMsg(errorM)}}}>Verify</button>
                </div>
                <p style={{color:"red"}}>{errorMsg.email}</p>
              </div>
              <div style={{display:"flex",flexDirection:"column",alignItems:"flex-start"}}>
                <div>
                  <p className={classes.inputLable}>Email OTP</p>
                </div>
                <div style={{display:"flex",flexDirection:"row",justifyContent:"space-between",width:"350px"}}>
                  {emailVerified?
                  [1,2,3,4,5,6].map(item=>{
                    return(<div className = {classes.emailOtpContainer}><input className={classes.otpInput} placeholder={0} type="text" ></input></div>)
                  }):
                  [1,2,3,4,5,6].map(item=>{
                    return(<div className = {classes.emailOtpContainer}><input className={classes.otpInput} placeholder={0} type="text" disabled ></input></div>)
                  })}
                </div>
              </div>
            </div>


            <div style={{display:"flex",flexDirection:"row",justifyContent:"space-between",width:"80%",margin:"auto"}}>
              <div style={{display:"flex",flexDirection:"column",alignItems:"flex-start"}}>
                <div>
                  <p className={classes.inputLable}>Mobile Number</p>
                </div>
                <div className = {classes.inputContainer}>
                  <div style={{height:"55px",width:"80px",borderRadius:"10px",backgroundColor:"#ddd",marginTop:"-5%",marginLeft:"-2%"}}>
                    <div style={{display:"flex",flexDirection:"row"}}>

                      <img width="30px" height="25px" style={{ paddingTop:"15px"}}src ={flag} alt ="flag"/>
                      <p>+91</p>
                    </div>
                  </div>
                  <input className = {classes.input} name="mobile" onChange={(e)=>handleInputChange(e)} style={{width:"255px"}}placeholder="Enter Your Mobile No"></input>
                  <button className = {classes.verifyBtn} onClick={()=>{if(formValues.mobile.length>0){setMobileVerified(true)}else{var errorM = {...errorMsg};errorM['mobile'] = "Provide Valid Mobile";setErrorMsg(errorM)}}}>Verify</button>
                </div>
                <p style={{color:"red"}}>{errorMsg.mobile}</p>
              </div>
              <div style={{display:"flex",flexDirection:"column",alignItems:"flex-start"}}>
                <div>
                  <p className={classes.inputLable}>Mobile OTP</p>
                </div>
                <div style={{display:"flex",flexDirection:"row",justifyContent:"space-between",width:"350px"}}>
                {mobileVerified?
                  [1,2,3,4,5,6].map(item=>{
                    return(<div className = {classes.mobileOtpContainer}><input className={classes.otpInput} placeholder={0} type="text" ></input></div>)
                  }):
                  [1,2,3,4,5,6].map(item=>{
                    return(<div className = {classes.mobileOtpContainer}><input className={classes.otpInput} placeholder={0} type="text" disabled ></input></div>)
                  })}
                </div>
              </div>
            </div>

            <div style={{display:"flex",flexDirection:"row",justifyContent:"space-between",width:"80%",margin:"auto"}}>
              <div style={{display:"flex",flexDirection:"column",alignItems:"flex-start"}}>
                <div>
                  <p className={classes.inputLable}>Password</p>
                </div>
                <div className = {classes.inputContainer}>
                  <input className = {classes.input} name="password" onChange={(e)=>handleInputChange(e)} placeholder="Enter Your Password" style={{width:"330px"}} type="password"></input>
                </div>
                <p style={{color:"red"}}>{errorMsg.password}</p>
              </div>
              <div style={{display:"flex",flexDirection:"column",alignItems:"flex-start"}}>
                <div>
                  <p className={classes.inputLable}>Confirm Password</p>
                </div>
                <div className = {classes.inputContainer}>
                  <input className = {classes.input} name="confirmPass" onChange={(e)=>handleInputChange(e)} style={{width:"330px"}} placeholder="Retype Your Password" type="password"></input>
                </div>
                <p style={{color:"red"}}>{errorMsg.confirmPass}</p>
              </div>
            </div>

          </div>
          <div style={{display:"flex",flexDirection:"column",alignItems:"flex-start",width:"600px"}}>
            <div  style={{paddingLeft:"15%"}}>
              <p className={classes.inputLable}>How do you know about carbanio?</p>
            </div>
            <div className = {classes.selectInput}>
            <FormControl variant="outlined" className={classes.formControl}>
              <InputLabel id="demo-simple-select-outlined-label">Click to Select...</InputLabel>
              <Select
                labelId="demo-simple-select-outlined-label"
                id="demo-simple-select-outlined"
                value={age}
                onChange={handleChange}
                label="Click to select...."
              >
                <MenuItem value="">
                  <em>None</em>
                </MenuItem>
                <MenuItem value={10}>I found in google/Internet search</MenuItem>
                <MenuItem value={20}>I received an Email/Newsletter from carbonia</MenuItem>
                <MenuItem value={30}>Carbonia marketing team approched me</MenuItem>
                <MenuItem value={40}>Known person refered</MenuItem>
                <MenuItem value={50}>I came to know through advertisement </MenuItem>
                <MenuItem value={60}>I found it on Social media</MenuItem>
                <MenuItem value={60}>I read a digital article about carbanio</MenuItem>
              </Select>
            </FormControl>
            </div>
          </div>
          <div style={{display:"flex",justifyContent:"center",height:"100px"}}>
            <div className = {classes.nextBtn}>
                  Next
            </div>
          </div>
        </Paper>
      </div>
    </div>
  );
}

export default App;
